







































































































import {Component, Vue} from 'vue-property-decorator';

@Component
export default class Transfer extends Vue {
    currentMonth = (new Date()).getFullYear() + '-' + ((new Date()).getMonth() + 1)
    isShowSearchDetail = false
    cityList = [
        {
            value: 'xem',
            label: 'xem'
        },
        {
            value: 'etc',
            label: 'etc'
        }
    ]
    assetType = ''

    transferTypeList = [
        {
            name: '普通转账',
            isSelect: true,
            disabled: false
        }, {
            name: '多签转账',
            isSelect: false,
            disabled: false
        }, {
            name: '跨链转账',
            isSelect: false,
            disabled: true
        }, {
            name: '聚合转账',
            isSelect: false,
            disabled: true
        }
    ]

    showSearchDetail() {
        this.isShowSearchDetail = true
    }

    hideSearchDetail() {
        this.isShowSearchDetail = false
    }

    changeCurrentMonth(e) {
        this.currentMonth = e
    }

    swicthTransferType(index) {
        const list: any = this.transferTypeList
        if (list[index].disabled) {
            return
        }
        list.map((item) => {
            item.isSelect = false
            return item
        })
        list[index].isSelect = true
        this.transferTypeList = list
    }
}
